import React from "react";
import "./home.css";
import bort2 from "./pic/bort2.JPG";
import bort3 from "./pic/bort3.JPG";
import citybort1 from "./pic/citybort1.jpg";
import citybort2 from "./pic/citybort2.jpg";

const experience = () => {
  return (
    <div className="margin">
      <h1 className="tab-header">体験しよう</h1>
      <p style={{ marginBottom: "40px" }}>
        体験型の観光スポットを紹介していきます
      </p>

      <div className="introduce">
        <h2 className="intro-header">その1 バスケットボート</h2>
        <img src={bort2} className="pic" />
        <img src={bort3} className="pic" />
        <p>
          ホイアンから少し出たところにバスケットボートというものがあり、川をガイドさんと一緒に回ることができます。川はとても広く開放的で自然を楽しむことができ、現地の人による
          カラオケやダンスといった水上パフォーマンスも楽しめます。
          ※料金は日本円で500円程度です
        </p>
      </div>

      <div className="introduce">
        <h2 className="intro-header">その2 ホイアン旧市街の舟</h2>
        <img src={citybort1} className="pic" style={{ width: "40%" }} />
        <img src={citybort2} className="pic" style={{ width: "40%" }} />
        <p>
          ホイアン旧市街の中にあるこちらの川でも舟に乗ることができます。
          こちらでは、街並みや夜のランタンによる幻想的な景色を舟からのんびり眺める
          ことが出来ます。※ちなみに料金は値段交渉だったりします
        </p>
      </div>
    </div>
  );
};

export default experience;
