import React from "react";
import "./home.css";
import { BrowserRouter, Route, Link } from "react-router-dom";
import { withRouter } from "react-router";
import map from "./pic/map.JPG";
import home from "./pic/home.JPG";

const Home = () => {
  return (
    <div className="margin">
      <h1 className="tab-header">ホイアンって?</h1>
      <p style={{ margin: "0px 10px" }}>
        ホイアンは、ベトナム中部のダナン空港から車で40分ほどのところにある観光都市の一つで、その古い街並みが世界遺産に登録されています。日本
        や中国と歴史的に強いつながりがあり、来遠橋(日本橋)などの日本と縁が深い歴史的建造物を見たり、ベトナムの文化体験が出来ます。
      </p>
      <img src={map} className="pic" style={{ width: "40%" }} />
      <img src={home} className="pic" style={{ width: "40%" }} />
    </div>
  );
};

export default Home;
